


/* For Desktop view */
@media (min-width: 1200px) {
body{
  background-color: lightgray;
}
.App {
  width: 400px;
  height: 100%;
  margin: 0 auto 5px auto;
  background-color: #121212 !important;
  padding-top: 20px;
  color: white;
}
}

.App{
  background-color: #121212;
}


/* Main Container */
.container{
  width: 90%;
  margin: 0 auto 5px auto;
  background-color: #121212;
  padding-top: 20px;
  color: white;
}
.header{
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-size: 24px;
  font-weight: 500;
  padding-top: 20px;
}

.sub-header{
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-size: 16px;
  font-weight: 400;
  padding-top: 20px;
}
.header-container .grid-container{
    display: grid;
    grid-template-columns: 80% 20%;
}
.header-container .grid-item {
  padding-bottom: 10px; 
}
.wallet-container{
  width: 70%;

}
.wallet-container .grid-container{
  display: grid;
  grid-template-columns: 50% 50%;
}

.wallet-container .grid-container2{
  display: grid;
  grid-template-columns: 30% 70%;
}

.header-container .wallet-container .grid-item {
  padding-bottom: 0 !important 
}


.wallet-container .grid-item .icon-text{
  font-size: 20px;
  padding: 5px;
}



/* Body Styling */
.body-heading{
  font-size: 24px;
  color: white;
  font-weight: 200;
  margin-bottom: 30px;
}

/* Job Card Styling */
.job-card .grid-container{
  display: grid;
  grid-template-columns: 30% 70%;
}

.job-card .grid-item {
  padding-bottom: 10px; 
}



/* Slider Styling */
.slide div::after {
  content: "";
  position: absolute;
  bottom: -1px;
  width: 100%;
  height: 50%;
  z-index: 10;
  background: rgb(198,198,198);
  background: linear-gradient(180deg, rgba(198,198,198,0) 0%, rgba(18,18,18,1) 74%);
}

/* Job Card */
.makeStyles-bigAvatar-10 {
  margin: 14px 0 !important;
}

.job-title{
  font-size: 20px;
}

.job-card .job-text{
  display: grid;
  grid-template-columns: 15% 85%;
}

.job-card .job-text .grid-item{
  padding-bottom: 5px; 
}


.job-card .grid-container{
  display: grid;
  grid-template-columns: 30% 70%;
}

.job-card .job-perks{
  display: grid;
  grid-template-columns: 25% 25% 25% 25%;
}

.job-card .job-perks .grid-item{
background-color: #3f3f3f;
color: white;
border-radius: 6px;
margin: 5px;
}

.perks-detail{
  margin: 15px 10px 5px 10px;
  text-align: center;
}

.perks-text{
  font-size: 14px !important;
}

.MuiButton-containedSecondary {
  color: white !important;
  background-color: #7d7d7d !important;
  padding: 10px 0 !important;
  width: 90%;
  margin-left: 15px !important;
}


.MuiButton-containedPrimary {
  color: white !important;
  background-color: #977cff !important;
  padding: 10px 0 !important;
  width: 90%;
}

.job-card .job-button{
  display: grid;
  grid-template-columns: 50% 50%;
}
.job-button .grid-item{
  width: 100%;
}
/* Progress Bar */
.profile-status{
  margin: 30px 0;
}
.profile-status .grid-container{
  display: grid;
  grid-template-columns: 10% 90%;
}
.profile-status .text{
  margin: 20px 0;
}
.react-sweet-progress-symbol{
  display: none !important;
}

.MuiDivider-root {
  border: none;
  height: 1px;
  margin: 0px;
  flex-shrink: 0;
  margin-top: 20px !important;
  background-color: #7d7d7d !important;
}